<template>
  <van-list
    class="cart"
    v-model="lazyLoad.isloading"
    :finished="lazyLoad.isFinished"
    :finished-text="lazyLoad.finishedText"
    :offset="50"
    @load="getLoad"

  >
  <van-nav-bar title="购物车" :right-text="isManage ? '管理' : '完成'"  @click-right="manage"/>
    <!-- 购物车无数据就显示空页面 -->
    <div v-if="this.cartLimitData.length == 0">
      <van-empty
        class="custom-image"
        image="https://img.yzcdn.cn/vant/custom-empty-image.png"
        description="快去逛逛加满你的购物车吧💗"
      />
    </div>
    
    <!-- 设置商品列表条swipercell组件 van-list列表懒加载-->
    <div  class="list" >
      <van-swipe-cell class="list-items" v-for="(item,index) in cartLimitData" :key="index">
        <div class="list-content clearfix">
          <!-- 单选框 -->
          <div class="fl checkbox-item">
            <van-checkbox shape="round" v-model="item.isCheck" @change="simpleSelect"/>
          </div>
          <!-- 商品信息 -->
          <div class="fl product-info clearfix">
            <!-- 图片 -->
            <div class="fl product-img">
              <img class="auto-img" :src="item.small_img" alt />
            </div>

            <!-- 内容 -->
            <div class="fl product-content">
              <!-- 商品标题 规格  -->
              <div class="product-text">
                <div class="clearfix product-name">
                  <span class="product-title fl">{{item.name}}</span>
                  <span class="fl product-rule">{{item.rule}}</span>
                </div>
                <div class="product-enname">{{item.enname}}</div>
              </div>
              <!-- 商品价格 和 选择数量 -->
              <div class="product-price">
                <div class="price fl">￥{{item.price}}</div>
                <div class="fr product-count">
                  <van-stepper
                    v-model="item.count"
                    :integer="true"
                    theme="round"
                    button-size="22"
                    disable-input
                    @change="modifyCount(item)"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>

        <template #right>
          <van-button square type="danger" text="删除" @click="deleteProduct([item.sid], index)" />
          <van-button square type="primary" text="收藏" />
        </template>
      </van-swipe-cell>
    </div>

    <!-- 提交订单  isManage管理绑定 切换-->
    <div v-if="isManage">
      <van-submit-bar class="submit-bar" :price="total" button-text="提交订单" button-type="info" @submit="goSubOrder">
        <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>

    <div v-else>
      <van-submit-bar class="submit-bar submit-bar-delete" button-text="删除选择" button-type="info" @submit="removeMoreProduct">
        <van-checkbox v-model="allCheck" @click="allSelect">全选</van-checkbox>
      </van-submit-bar>
    </div>

  </van-list>
</template>

<script>
import "../../assets/css/cart.less";
import { SubmitBar } from 'vant';
export default {
  data() {
    return {
      isHas: false, //默认无数据  //单选 在forEach已添加为isCheck
      count: 1, //控制商品数量
      cartLimitData: [], //购物车限制数据
      allCartData: [], //购物车全部数据
      //截取控制加载数据 5条
      limitData: 6,
      //懒加载购物车商品条 。 双向绑定 商品列表list
      lazyLoad: {
        isLoading: true, //处于加载状态
        isFinished: false, //是否完成加载所有数据
        finishedText: "没有数据可加载啦~"
      },
      allCheck: false, //全选
      isManage: true, //控制 管理 删除商品条
      total:0,
    };
  },
  methods: {
    //axios获取购物车数据
    getCart() {
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            //先处理数据添加属性isCheck 再 存储购物车数据 然后赋值
            result.data.result.forEach(v => {
              v.isCheck = false;
            });
            this.allCartData = result.data.result;
            
            //截取 限制数据 用于懒加载
            this.cartLimitData = this.allCartData.splice(0, this.limitData);
            
            //判断总数量 截取后是否还有数据
            if (this.allCartData.length > 0) {
              this.lazyLoad.isLoading = false;
            } else {
              this.lazyLoad.isLoading = true;
              this.lazyLoad.isFinished = true;
              this.lazyLoad.finishedText = "";
            }
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //懒加载购物车数据 截取后剩余的数据
    getLoad() {
      
      setTimeout(() => {
        this.cartLimitData.push(...this.allCartData.splice(0, this.limitData));
        if (this.allCartData.length == 0) {
          this.lazyLoad.isLoading = true; //剩余为0就不再加载
          this.lazyLoad.isFinished = true;
        }
      }, 2000);
    },

    //全选 状态
    allSelect(){
      this.allCartData.forEach(v=>{ //未加载出来的商品 也需要 全选
        v.isCheck = this.allCheck;
      })
      this.cartLimitData.forEach(v=>{
        v.isCheck = this.allCheck;
      })
      this.sum()
    },
    //单选 状态
    simpleSelect(){
      this.sum();
      for(let i =0;i<this.cartLimitData.length;i++){
        if(!this.cartLimitData[i].isCheck){ //1个未勾选 
          this.allCheck =false;
          return;
        }
      }
      //全部购满的话
      this.allCheck = true;
    },

    //管理  取反
    manage(){
      this.isManage =!this.isManage;
    },
    
    //修改购物车数量 步进器change=‘’
    modifyCount(item){
     
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/modifyShopcartCount',
        data:{
          appkey: this.appkey,
          sid:item.sid,
          count: item.count,
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 6000){
              this.sum(); //修改数量计算总金额
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },

    //单个删除购物车商品 根据sid
    deleteProduct(sids ,index){
      //获取购物车商品sid
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/removeShopcart',
        data:{
          appkey: this.appkey,
          sids:JSON.stringify(sids),//传递数组。需要序列化唯一标识
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 7000){
            //删除当前点击的数据
            this.cartLimitData.splice(index,1);
            this.sum( );
            // this.lazyLoad.finishedText='';
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },
    // sumbit管理删除多个购物车商品
    removeMoreProduct(){
      // 视图数据处理 获取删除购物车sid 为true
      let sids =[];
      this.cartLimitData.forEach(v=>{
        if(v.isCheck){
          sids.push(v.sid)
        }
      })
      
      //后台数据处理
      let tokenString = localStorage.getItem("token");
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method:'POST',
        url:'/removeShopcart',
        data:{
          appkey: this.appkey,
          sids:JSON.stringify(sids),//传递数组。需要序列化唯一标识
          tokenString
        }
      }).then(result => {
          this.$toast.clear();
          
          if(result.data.code == 7000){
            //删除当前点击的数据
            for(let i = this.cartLimitData.length-1;i>0;i--){
              if(this.cartLimitData[i].isCheck){
                this.cartLimitData.splice(i,1);
              }
            }
            this.sum( );
            //
            if(this.cartLimitData.length == 0){
              this.allCheck = false;
              this.isManage = true;
              //this.isHas = false;
              this.lazyLoad.finishedText='';
            }
          }
          
      }).catch(err => {
          this.$toast.clear();
          
      })
    },

    //计算总金额total
    sum(){
      //初始化total
      this.total =0;
      this.cartLimitData.forEach(v=>{
        if(v.isCheck){
          this.total += v.count *v.price*100
        }
      })

    },

    //跳转提交订单页 需要带id 获取选择的商品
    goSubOrder(){
      //获取选择商品的sid 然后遍历商品把aid 带参数跳转到order.vue
      let sids =[];
    
      this.cartLimitData.forEach(v=>{
        
        if(v.isCheck){
          sids.push(v.sid);
          this.$router.push({name:'Pay',query:{sids:sids.join('-')}});
        }else{
          this.$toast('请选择需要提交的商品哟');
        }
      })
      
    }
  },

  created() {
    this.getCart();
    //隐藏导航
    this.$store.state.bottomTabbar = false;
  },
  // 切换组件生周期钩子，销毁函数
  beforeDestroy() {
    this.$store.state.bottomTabbar = true;
  }
};
</script>

<style lang="scss" scoped>
</style>